<template>
    <div class="p-4 ">
        <div class="row">
            <div class="offset-1 col-9 bg-white p-5 rounded-lg shadow-lg">
                <div class="text-center">
                    <img src="@/static/photo/sma1.jpg" class="kinoLightBox img-fluid z-depth-1 mx-auto" style="border-radius: 20px;">
                </div>
                <br>
                <br>
                <p class="pl-5 font-bold font-times text-uppercase text-2xl">SMA N 1 Tambang Ulung</p>
                <p class="p-4 text-justify font-times text-xl" v-html="sejarah">
                </p>
            </div>
        </div>
        <br>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    data(){
        return{
            sejarah:""      
        }
    },
    layout:"awal",
    mounted() {
        let fd = new FormData()
        fd.append('data', `select * from sekolah`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            this.sejarah=res.data[0].sejarah
            this.$forceUpdate()
        })
    },
}
</script>